.app.rubikTimer h2 {
    text-align: center;
    color:rgb(126, 160, 224);
}

.app.rubikTimer h3 {
    font-size: 50px;
    text-align: center;
    color:rgb(126, 160, 224);
}

.app.rubikTimer h3.green-timer {
    color: greenyellow;
}

.app.rubikTimer h3.running-timer {
    font-size: 80px;
}

.nonSelectable {
    user-select: none;
}

.app.rubikTimer h2.megaminx {
    text-align: left;
    font-family: 'Courier New', Courier, monospace;
    font-weight: bold;
    font-size: 24px;
    white-space: preserve;
}

@media (orientation: landscape) and (hover: none) and (pointer: coarse) {
    .app.rubikTimer h2.two {
        font-size: 80px;
    }

    .app.rubikTimer h2.three, .app.rubikTimer h2.bld {
        font-size: 60px;
    }

    .app.rubikTimer h3.running-timer {
        font-size: 150px;
    }

    .app.rubikTimer h2.megaminx {
        font-size: 22px;
    }
}